import { createGlobalStyle } from 'styled-components'


const GlobalStyle = createGlobalStyle`
  * {
    font-family: 'Mitr', sans-serif;
    
  }
  body {
    // background: url(https://audit.hiko.network/wp-content/uploads/2022/11/background2-scaled.jpg) no-repeat center ;
    // background: #0b1320;
    background-image: radial-gradient(circle, #0014ff, #030a41, #0b225d);

    img {
      height: auto;
      background-size:cover;
      background: no-repeat;
      max-width: 100%;
    }
  }
`

export default GlobalStyle
